.parent {
  @apply flex justify-center items-center flex-col m-8 mt-0 overflow-hidden;

  .heading {
    @apply text-2xl font-bold text-center my-6 text-white relative;
  }

  input {
    @apply block w-2/3 bg-white bg-opacity-10 border-r-2 border-transparent  py-3  px-4 placeholder:text-slate-400 outline-none text-slate-50 transition-colors;
  }

  .button {
    @apply bg-orange-400 text-white block py-2.5 w-full transition-colors hover:bg-orange-600 font-bold text-lg;
  }
  select {
    @apply block w-2/3 bg-white bg-opacity-50 py-3  px-4 placeholder:text-slate-400 outline-none text-slate-50 transition-colors;
    option {
      @apply text-black bg-slate-600;
    }
  }

  ul {
    @apply mt-2 flex justify-center flex-wrap gap-x-3 gap-y-1.5;
    li {
      @apply cursor-pointer flex w-64 bg-blue-300  text-black font-bold;
      span {
        @apply font-bold text-white underline;
      }
    }
  }
}
